import {connect} from "react-redux";
import React, {Component} from "react";
import PropTypes from "prop-types";
import OnlineUtils from "eshop/utils/OnlineUtils";

export default class TableErrorSection extends Component {

    constructor(props) {
        super(props);
        this.state = {currency: "zł"};
    }

    render () {
        const {reasonData, descriptions} = this.props;
        return ( 
            <div key={reasonData.planName} className="l-row u-padding-left-xl u-padding-top" key={"customer-verification-message-" + reasonData.msisdn}>
                <div className="l-col l-col-10">
                    {reasonData.planName &&
                        <div> 
                            <strong>{reasonData.planName}</strong>
                            { reasonData.msisdn &&( <span>
                                <span className="u-padding-left-s">
                                    {descriptions.msisdnCaption || "Nr telefonu "}
                                    {OnlineUtils.formatMsisdn(reasonData.msisdn)}
                                </span>
                            </span>)}
                        </div>
                    }
                    {reasonData.deviceName &&
                        <div className="u-font-bold">
                            {reasonData.deviceName}
                        </div>
                    }
                    <div className="u-padding-top-s">
                       {reasonData.reasons.map((reason, key) => {
                            return ( <div key={"customer-verification-reason-" + key} className="u-padding-top-s">
                                <strong>{descriptions.declineReasonCaption || "Powód:"} </strong> {reason}
                            </div> )
                        })}
                    </div> 

                </div>
                <div className="l-col l-col-2 u-right u-font-bold vertical-center u-text-right">
                {(!!reasonData.deposit && reasonData.deposit > 0) &&
                    <strong>
                        {reasonData.deposit} {this.state.currency}
                    </strong>
                }
                </div>
            </div>
        )
    }

    renderMsisdn = (msisdn) => {
        const {descriptions} = this.props;
        if(reasonData.msisdn) {
           return (<div>
                {descriptions.msisdnCaption || "Nr telefonu"} {OnlineUtils.formatMsisdn(msisdn)}
            </div>)
        }
    }
}

TableErrorSection.propTypes = {
    reasonData: PropTypes.any.isRequired,
    descriptions: PropTypes.any.isRequired
}
